<template>
  <section class="container">
    
    <div class="pv-lg flex v-center">
        <el-breadcrumb separator-class="icon icon-right">
            <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item :to="{ path: '/album-detail'}">专辑</el-breadcrumb-item>
            <el-breadcrumb-item>专辑详情</el-breadcrumb-item>
        </el-breadcrumb>
    </div>
    <el-row :gutter="20">
        <el-col :sm="15" :md="16" :lg="17">

            <!-- swiper1 -->
            <div class="gallery-top">
                <swiper :options="swiperOptionTop" ref="swiperTop">
                    <swiper-slide class="slide" v-for="(item , index) in List" :key="index" :style="{backgroundImage:'url(' + item.Avatar + ')'}">
                    </swiper-slide>
                    <div class="swiper-button-next swiper-button-white" slot="button-next"></div>
                    <div class="swiper-button-prev swiper-button-white" slot="button-prev"></div>
                </swiper>
            </div>
            <!-- swiper2 Thumbs -->
            <div class="gallery-thumbs">
                <swiper :options="swiperOptionThumbs"  ref="swiperThumbs">
                    <swiper-slide  v-for="(item , index) in List" :key="index" :style="{backgroundImage:'url(' + item.Avatar + ')'}">
                    </swiper-slide>
                </swiper>
            </div>

            <div class="card info-block mt-lg">
                <el-row :gutter="20">
                    <el-col :sm="6">
                        <div class="main-pic">
                            <img src="http://oz3n01eow.bkt.clouddn.com/a3.jpeg" alt="">
                        </div>
                        <ul class="ul-list list-h share-icons">
                            <li><i class="icon icon-qq-flat"></i></li>
                            <li><i class="icon icon-weixin"></i></li>
                            <li><i class="icon icon-weibo"></i></li>
                        </ul>
                    </el-col>
                    <el-col :sm="18">
                        <div class="name">{{ infos.name }}</div>
                        <el-row :gutter="10"  class="info-list">
                            <el-col :sm="4" class="label">发行时间</el-col> 
                            <el-col :sm="20" class="info">2018年1月1日</el-col>
                            <el-col :sm="4" class="label">专辑介绍</el-col>
                            <el-col :sm="20" class="info">{{ infos.desc }}</el-col>

                            <el-col :sm="4" class="label">标签</el-col>
                            <el-col :sm="20" class="info">
                                <el-tag class="tag-grey">制服诱惑</el-tag>
                                <el-tag class="tag-grey">爆乳</el-tag>
                            </el-col>
                        </el-row>
                        <el-tabs v-model="activeName2" type="card" @tab-click="handleClick" class="tabs-flat mt-lg">
                            <el-tab-pane label="普通" name="first">
                                <el-row :gutter="20">
                                    <el-col :sm="20">
                                        <el-row :gutter="10"  class="info-list">
                                            <el-col :sm="4" class="label">发行时间</el-col> 
                                            <el-col :sm="20" class="info">2018年1月1日</el-col>
                                            <el-col :sm="4" class="label">高清图数量</el-col>
                                            <el-col :sm="20" class="info">9930</el-col>
                                            <el-col :sm="4" class="label">图片格式</el-col>
                                            <el-col :sm="20" class="info">JPEG / PNG</el-col>
                                            <el-col :sm="4" class="label">文件大小</el-col>
                                            <el-col :sm="20" class="info">127M</el-col>
                                        </el-row>
                                    </el-col>
                                    <el-col :sm="4">
                                        <el-button type="warning">订阅</el-button>
                                    </el-col>
                                </el-row>
                            </el-tab-pane>
                            <el-tab-pane label="VIP通道" name="second">
                                <div class="p-lg tc-light fs-xl text-center">您还没有开动VIP会员</div>
                                <div class="text-center"><el-button type="warning">开通VIP</el-button>  </div>
                            </el-tab-pane>
                        </el-tabs>
                    </el-col>
                </el-row>
            </div>
        </el-col>
        <el-col :sm="9" :md="8" :lg="7" class="mb-lg">
            <div class="card">
                <div class="card-title">模特信息</div>
                <div class="card-body">
                    <div class="flex">
                        <avatar src="http://oz3n01eow.bkt.clouddn.com/a2.jpeg" class="mr" url="/model-detail"></avatar>
                        <div class="f-body">
                            <div class="title">张予曦</div>
                            <p class="desc">江西南昌</p>
                        </div>
                        <el-button type="primary" size="small">关注</el-button>
                    </div>
                    <div class="data-bar mt-xl">
                        <div class="data-item">
                            <div class="num">32142</div>
                            <div class="label">人气</div>
                        </div>
                        <div class="data-item">
                            <div class="num">3921</div>
                            <div class="label">粉丝</div>
                        </div>
                        <div class="data-item">
                            <div class="num">12</div>
                            <div class="label">专辑</div>
                        </div>
                        <div class="data-item">
                            <div class="num">190</div>
                            <div class="label">视频</div>
                        </div>
                    </div>
                    <div class="info-text mt-md">
                        <p>三围：80 / 58 / 90</p>
                        <p>发行时间：2018-01-04</p>
                        <p>简介：这是模特的介绍内容，今年18岁刚进入模特行业半年时间，是一个不错的女孩，目前处于单身状态</p>
                    </div>
                </div>
            </div>
            <div class="card mt-lg">
                <div class="card-title">模特专辑</div>
                <el-row :gutter="20" class="grid-list">
                    <el-col :sm="12" v-for="(item , index) in List" :key="index" class="mb" v-if="index<4">
                        <g-item :img-src="item.CoverImage" 
                                :title="item.Title" 
                                :desc="'粉丝:' +item.ViewNum"
                                img-height="150"
                                url="/album-detail"
                                clear-ph>
                        </g-item>
                    </el-col>
                </el-row>
            </div>
            <div class="card mt-lg">
                <div class="card-title">模特专辑</div>
                <el-row :gutter="20" class="grid-list">
                    <el-col :sm="12" v-for="(item , index) in List" :key="index" class="mb" v-if="index<10 && index>5">
                        <g-item :img-src="item.CoverImage" 
                                :title="item.Title" 
                                :desc="'粉丝:' +item.ViewNum"
                                img-height="150"
                                url="/album-detail"
                                clear-ph>
                        </g-item>
                    </el-col>
                </el-row>
            </div>
        </el-col>
    </el-row>


  </section>
</template>

<script>


export default {
    name: "detail",
    components: {
        
    },
    data() {
        return {
            List: [],
            swiperOptionTop: {
                spaceBetween: 10,
                loop: true,
                loopedSlides: 5, //looped slides should be the same
                navigation: {
                    nextEl: '.swiper-button-next',
                    prevEl: '.swiper-button-prev'
                }
            },
            swiperOptionThumbs: {
                spaceBetween: 10,
                slidesPerView: 8,
                touchRatio: 0.2,
                loop: true,
                loopedSlides: 5, //looped slides should be the same
                slideToClickedSlide: true,
            },
            activeName2: 'first',
            infos:{
                name : 'UTC - 李珊珊',
                desc : '太平洋影音公司秉承“将中国传统音乐文化创新发展”的宗旨，不断求变、求新，创作出众多既具有浓厚中国特色又表现出新风格、新面貌的音像制品。经历无数个日日夜夜，苦心钻研，反复尝试、录音，太平洋影音公司全新原创产品《发烧神笛》隆重面世。一张敢于叫板的发烧天碟，带给你的不仅是天籁之音，更多的是震撼，是惊讶，是万分的百听不厌，难舍难离。'
            }
        }
    },
    mounted() {
      this.$nextTick(() => {
        const swiperTop = this.$refs.swiperTop.swiper
        const swiperThumbs = this.$refs.swiperThumbs.swiper
        swiperTop.controller.control = swiperThumbs
        swiperThumbs.controller.control = swiperTop
      })
    },
    created () {
        this.getListData()
    },
    methods : {

        getListData() {
            this.axios.get('https://jason588.github.io/data.json').then((res) => {
                this.List = res.data.List
            })
        },
        handleClick(tab, event) {
            console.log(tab, event);
        }
    },
  
};
</script>

<style lang="scss" scoped>
@import "../assets/scss/base";


</style>


