<template>
    <div  class="g-item" :class="{'clear-ph':clearPh}">
        <div class="media" :style="{ backgroundImage: 'url('+ imgSrc +')' , height: imgHeight +'px'}" :class="{'has-border':hasBorder}">
			<div class="tag" v-if="tag">{{ tag }}</div>
		</div>
		<div class="info-bar" >
			<avatar :src="avatarSrc" v-if="avatarSrc"></avatar>
			<div class="text-wrap" >
				<div class="title">{{title}}</div>
				<div class="desc">{{desc}}</div>
			</div>
			<div class="right-wrap">
				<slot name="right"></slot>
			</div>
		</div>
		<div class="mask-wrap" v-if="hasMask">
			<slot name="mask"></slot>
		</div>
		<router-link :to="url" class="link"></router-link>
		<slot></slot>
    </div>
</template>

<script>

export default {
    name: 'gItem',
    props:{
        url: {
          type : String,
          default : '/'
        },
        imgSrc:{
          type : String,
          default : ''
        },
        imgHeight:{
          type : String,
          default : '120'
        },
        hasMask:{
          type : Boolean,
          default : false
        },
        hasBorder:{
          type : Boolean,
          default : false
        },
        clearPh:{
          type : Boolean,
          default : false
        },
        avatarSrc:  String,
        title: String,
        desc: String,
        tag: String,
    },
    data(){
        return{

        }
    },
    methods:{

    }
}
</script>

<style lang="scss">
@import "../assets/scss/base";

.g-item{
	background: #fff;
	border-radius: $border-radius;
	display: block;
	transition: all .3s;
	position: relative;
	overflow: hidden;
	.media{
		transition: all .3s;
		border-radius: $border-radius;
		overflow: hidden;
		position: relative;
		display: block;
		background-repeat: no-repeat;
		background-position: center;
		background-size: cover;
		background-color: #eee;
		&.has-border{
			border: solid #fff;
			border-width: 10px 10px 0;
		}
		img{
			width: 100%;
			display: block;
			transition: all .3s
		}
		.tag{
			position: absolute;
			right: 0;
			top: 0;
			border-radius: 0 $border-radius 0 $border-radius ;
			background: rgba(#000,.8);
			font-size: 12px;
			padding: 4px 10px;
            color: #fff;
            
		}
	}
	.info-bar{
		background-color: #fff;
		padding: 10px;
		display: flex;
		align-items: center;
		.avatar{
			margin-right: 10px;
			background-color: #eee;
		}
		.text-wrap{
			flex: 1;
			white-space: normal;
			overflow: hidden;
			line-height: 1;
			.title{
				@include ellipsis;
			}
			.desc{
				margin-top: 5px
			}
		}
		.right-wrap{
			margin-left: 10px;
		}
	}
	&.clear-ph{
		box-shadow: none !important;
		.info-bar{
			padding-left: 0;
			padding-right: 0
		}
	}
	.link{
		@include position(absolute,0 0 0 0);
		z-index: 10;
	}
	.mask-wrap{
		@include position(absolute,0 0 0 0);
		background-color: rgba($primary,.9);
		display: flex;
		align-items: center;
		justify-content: center;
		opacity: 0;
		transform: scale(1.1);
		transition: all .3s;
		z-index: 9;
		> div{
			width: 100%;
			color: #fff;
			padding: 0 30px;
			box-sizing: border-box;
			.name{
				font-size: 18px;
				margin-bottom: 10px;
			}
			.info{
				font-size: 12px;
				line-height: 1.8;
			}
		}
	}
	&:hover{
		box-shadow: 0 4px 10px rgba(#000,.2);
		transform: scale(1.01);
		.mask-wrap{
			opacity: 1;
			transform: scale(1);
		}
	}
}


</style>



