<template>
  <div class="avatar" :style="{width:size + 'px', height: size + 'px',backgroundImage:'url('+ src +')'}">
    <router-link :to="url" v-if="url" class="link"></router-link>
    <slot></slot>
  </div>
</template>

<script>
export default {
  name: 'avatar',
  props:{
      size : {
          type : Number,
          default : 40
      },
      src: String,
      url: String

  },
  data(){
    return{

    }
  },
  methods:{

  }
}
</script>

<style lang="scss">
@import "../assets/scss/base";
.avatar{
    border-radius: 50%;
    position: relative;
    background-color: #f6f6f6;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    .badge{}
    .link{
      @include position(absolute,0 0 0 0);
      z-index: 9;
    }
}

</style>



