<script>
export default {
    components:{
        
    },
    data() {
      return {
        links:['商务合作','意见反馈','关于我们','联系我们','版权声明']
      }
    }
}
</script>

<style lang="scss">
@import "../../assets/scss/base";
.white-wrap{
  background-color: #fff;
  padding-top: 30px;
  padding-bottom: 30px;
  .logo-left{
    width: 100%;
    max-width: 90px;
  }
  .f-body{
    .links{
      margin-bottom: 10px;
    }
    
  }
  .r-img{
    text-align: right;
    img{
      width: 100px;
    }
  }
}
.share-icons{

  li{
    + li{
      margin-left: 30px
    }
  }
  .icon{
    font-size: 32px;
    color: #a5adac
  }
}
.black-wrap{
  background-color: $dark;
  color: #929292;
  font-size: $font-md;
  padding: 20px 0;
  text-align: center;
  .cpright{
    line-height: 2;
  }
}


</style>



