<template>
  <div class="header">
    <div class="container">
      <img src="../../assets/img/logo.png" alt="" class="logo">
      <div class="center-wrap">
        <ul class="ul-list list-h links ml-xxl">
          <li><router-link to="/user">个人中心</router-link></li>
          <li><router-link to="/refill">充值页面</router-link></li>
          <li><router-link to="/order">订单页面</router-link></li>
          <li><router-link to="/model-detail">模特详情</router-link></li>
          <li><router-link to="/album-detail">专辑详情</router-link></li>
        </ul>
      </div>
      <div class="right-wrap flex">
        <el-button type="primary" @click="showLogin=true">登录</el-button>
        <el-button type="warning" @click="showRegister=true">注册</el-button>
      </div>
    </div>
    <el-dialog title="登录" :visible.sync="showLogin" width="30%" :before-close="loginClose" center>
      <div class="form-wrap">
        <el-form :model="loginForm" label-width="80px" label-position="right">
          <el-form-item label="用户名" >
            <el-input v-model="loginForm.name" auto-complete="off" placeholder="请输入用户名"></el-input>
          </el-form-item>
          <el-form-item label="密码" >
            <el-input type="password" v-model="loginForm.password" auto-complete="off" placeholder="请输入密码"></el-input>
          </el-form-item>
          <el-form-item label="" >
            <el-checkbox label="下次自动登录" name="loginForm.type"></el-checkbox>
          </el-form-item>
        </el-form>
        <div class="text-center">
          <el-button type="primary" @click="showLogin = false" class="btn-lg">登录</el-button>
        </div>
        <div class="other-login">
          <div class="fs-sm mb-lg">使用合作账号进行登录或 <span class="tc-primary ml">注册</span></div>
          <ul class="ul-list icons">
            <li><i class="icon icon-qq-flat"></i></li>
            <li><i class="icon icon-weixin"></i></li>
            <li><i class="icon icon-weibo"></i></li>
          </ul>
        </div>
      </div>
    </el-dialog>
    <el-dialog title="新用户注册" :visible.sync="showRegister" width="30%" :before-close="resClose" center>
      <div class="form-wrap clear-mb">
        <el-form :model="resForm"  label-position="right">
          <el-form-item label="账号(邮箱/手机号码)" >
            <el-input v-model="resForm.name" auto-complete="off" placeholder="请输入邮箱/手机号码"></el-input>
          </el-form-item>
          <el-form-item label="昵称" >
            <el-input v-model="resForm.name" auto-complete="off" placeholder="请输入您的昵称"></el-input>
          </el-form-item>
          <el-form-item label="密码" >
            <el-input type="password" v-model="resForm.password" auto-complete="off" placeholder="请输入密码"></el-input>
          </el-form-item>
          <el-form-item label="确认密码" >
            <el-input type="password" v-model="resForm.password" auto-complete="off" placeholder="请输入密码"></el-input>
          </el-form-item>
          <el-form-item label="" class="mt">
            <el-checkbox label="我已阅读并订阅同意" name="resForm.type"></el-checkbox>
            <span class="tc-primary">《优果网络服务协议》</span> 
          </el-form-item>
        </el-form>
        <div class="text-center mt-lg">
          <el-button type="primary" @click="showRegister = false" class="btn-lg">注册</el-button>
        </div>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
    name: 'v-header',
    data() {
      return {
        showLogin:false,
        showRegister:false,
        loginForm: {
          name: '',
          password: '',
          type:[]
        },
        resForm: {
          name: '',
          password: '',
          type:[]
        }
      };
    },
    methods: {
      loginClose() {
        console.log('关闭');
        this.showLogin = false
      },
      resClose() {
        console.log('关闭');
        this.showRegister = false
      }
    }
}
</script>

<style lang="scss">
@import "../../assets/scss/base";
.header{
  height: $header-height;
  background-color: $header-bg;
  .container{
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 100%;
  }
  .logo{
    height: 50px;
    margin-right: 30px;
  }
  .center-wrap{
    flex: 1;
  }
  .right-wrap{
    margin-left: 30px;
  }
}

.form-wrap{
  width: 80%;
  margin: 0 auto;
  .el-button{
    width: 150px;
  }
  .el-checkbox__label{
    color: $text-light
  }
  &.clear-mb{
    .el-form-item{
      margin-bottom: 0
    }
  }
}
.other-login{
  text-align: center;
  margin-top: 30px;
  padding-top: 30px;
  border-top: 1px dashed $border-light;
  .icons{
    display: flex;
    align-items: center;
    justify-content: center;
    li{
      padding: 0 20px;
    }
    .icon{
      font-size: 40px;
      color: #ddd;
    }
  }
}
</style>



