<template>
  <section class="container pv-lg">
    <div class="tool-bar">
        <div class="left">
          <el-select v-model="value" placeholder="请选择">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </div>
        <div class="right flex">
          <div class="input-btn-group">
            <el-input placeholder="请输入内容" v-model="input5" class="has-left-icon">
              <i slot="prefix" class="icon icon-search icon-sm ml tc-grey-l"></i>
            </el-input>
            <el-button class="btn-right">搜索</el-button>

          </div>
        </div>
    </div>


    <el-row :gutter="20" class="grid-list mt-lg">
      <el-col :sm="12" :md="8" :lg="6" v-for="(item , index) in List" :key="index" class="mb-lg">
        <g-item :img-src="item.CoverImage" 
                :avatar-src="item.Avatar" 
                :title="item.Title" 
                :desc="'粉丝:' +item.ViewNum"
                img-height="240"
                url="/album-detail"
                has-mask
                has-border>
                <div class="infos" slot="mask">
                  <div class="name">{{ item.NickName }}</div>
                  <p class="info">上架时间: {{item.UpdateTime }}</p>
                  <p class="info">订阅量: {{ item.num2 }}</p>
                  <p class="info">三围: 90 / 40 / 90</p>
                </div>
        </g-item>
      </el-col>
    </el-row>
    <div class="pagination-bar hide-xs">
      <el-pagination background layout="prev, pager, next" :total="1000">
      </el-pagination>
      <el-button class="btn-dark btn-lg puls-width">全部9283个</el-button>
    </div>



  </section>

  
</template>

<script>


export default {
  name: "home",
  components: {
    
  },
  data() {
    return {
      List: [],
      options: [{
        value: '选项1',
        label: '全部写真'
      }, {
        value: '选项2',
        label: '写真集1'
      }, {
        value: '选项3',
        label: '写真集2'
      }, {
        value: '选项4',
        label: '写真集3'
      }, {
        value: '选项5',
        label: '写真集4'
      }],
      value: '选项1',
      input5 : ''
    }
  },
  created () {
    this.getListData()
  },
  methods : {
    login() {
      
    },
    getListData() {
      this.axios.get('https://jason588.github.io/data.json').then((res) => {
        this.List = res.data.List
      })
    }
  },
  
};
</script>



