<template>
  <section class="container">
    
    <div class="pv-lg flex v-center">
        <el-breadcrumb separator-class="icon icon-right">
            <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item>个人中心</el-breadcrumb-item>
            <el-breadcrumb-item>充值</el-breadcrumb-item>
        </el-breadcrumb>
    </div>
    <div class="card">
        <div class="card-title">账户信息核对</div>
        <div class="flex">
            <div class="thumb bg-box mr-md" :style="{backgroundImage:'url(http://oz3n01eow.bkt.clouddn.com/a2.jpeg)'}"></div>
            <div class="f-body info-body">
                <h4>KD大师</h4>
                <p>优果账号：kab9388490204</p>
                <p>优果号 ： 933928394</p>
            </div>
        </div>
    </div>
    <div class="card mt-lg">
        <div class="card-title">选择充值金额</div>
        <el-row :gutter="20">
            <el-col :xs="12" :sm="8" :md="6" :lg="4"  v-for="(item,index) in Money" :key="index" class="mb-lg" > 
                <div class="grey-card" :class="{'active' : index==3}">{{item}}元</div>  
            </el-col>
        </el-row>
    </div>
    <div class="card mt-lg">
        <div class="card-title">升级VIP享受至尊特权</div>
        <el-row :gutter="20">
            <el-col :xs="12" :sm="8" :md="6"   v-for="(item,index) in 4" :key="index"  > 
                <div class="grey-card ph-xl flex" >
                    <div class="icon-left icon icon-moneybag mr-lg"></div>
                    <div class="f-body">
                        <h4>黄金VIP</h4>
                        <div class="price">999元</div>
                        <a href="/" class="link">详情 >></a>
                    </div>
                </div>  
            </el-col>
        </el-row>
    </div>
    <div class="card mt-lg mb-lg">
        <div class="card-title">选择充值方式</div>
        <el-row :gutter="20">
            <el-col :xs="12" :sm="8" :md="6"  v-for="(item,index) in payType" :key="index" class="mb-lg" > 
                <div class="grey-card" :class="{'active' : index==1}">{{item}}</div>  
            </el-col>
        </el-row>
        <div class="pv-lg text-right">
            <el-button type="warning" class="puls-width">充值</el-button>
        </div>
    </div>

  </section>
</template>

<script>


export default {
    name: "detail",
    components: {
        
    },
    data() {
        return {
            List: [],
            Money: [30,50,100,200,300,400,500,1000,2000],
            payType: ['支付宝','微信支付','易宝支付','PayPal']
        }
    },
    mounted() {
      
    },
    created () {
        this.getListData()
    },
    methods : {

        getListData() {
            this.axios.get('https://jason588.github.io/data.json').then((res) => {
                this.List = res.data.List
            })
        }
    },
  
};
</script>

<style lang="scss" scoped>
@import "../assets/scss/base";

 
</style>


