<template>
  <section class="container">
    
    <div class="pv-lg flex v-center">
        <el-breadcrumb separator-class="icon icon-right">
            <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item>个人中心</el-breadcrumb-item>
            <el-breadcrumb-item>订单</el-breadcrumb-item>
        </el-breadcrumb>
    </div>
    <div class="card">
        <div class="card-title">全部订单</div>
        <el-table ref="multipleTable" :data="List" tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectionChange" >
            <el-table-column type="selection" width="55"> </el-table-column>
            <el-table-column label="专辑/视频" width="300">
                <template slot-scope="scope">
                    <div class="flex">
                        <img :src="scope.row.Avatar" style="width:60px" alt="">
                        <div class="tc-dark ml">{{scope.row.NickName}}</div>
                    </div>
                </template>
            </el-table-column>
            <el-table-column prop="Price" label="价格" > </el-table-column>
            <el-table-column prop="ViewNum" label="数量" show-overflow-tooltip> </el-table-column>
            <el-table-column  label="操作" width="150" show-overflow-tooltip> 
                <template slot-scope="scope">
                    <i @click="handleClick(scope.row)" class="icon-btn tc-primary el-icon-edit"></i>
                    <i class="icon-btn tc-red el-icon-delete"></i> 
                </template>
            </el-table-column>
        </el-table>
        <div class="table-footer">
            <el-button type="danger" icon="el-icon-delete" v-if="showDel">删除</el-button>
            <div class="right-text">总共 <span class="tc-primary">10</span> 个商品</div>
        </div>
    </div>
    <div class="card mt-lg mb-lg">
        <div class="card-title">结账</div>
        <div class="grey-block text-right tc-grey">
            <p>商品总价 <span class="fs-xxl tc-primary ph-lg">99金果</span> <span class="ph-lg">=</span> 9.90元(金果结算价) </p>
            <p>赠送银果0个</p>
        </div>
        <div class="text-right tc-light pv-lg">
            当前账户: 金果 <span class="tc-primary fs-lg ph">92个</span> 
            银果 <span class="tc-primary fs-lg ph">192个</span> 
            等级 <span class="tc-primary fs-lg ph">5级</span>
        </div>
        <div class="pv-lg flex jc-sb">
            <el-button class="btn-dark btn-lg">充值</el-button>
            <div class="right">
                <el-button type="primary" class=" btn-lg">金果支付</el-button>
                <el-button type="warning" class=" btn-lg ml-lg">现金支付</el-button>
            </div>
        </div>
    </div>
    

  </section>
</template>

<script>


export default {
    name: "detail",
    components: {
        
    },
    data() {
        return {
            List: [],
            multipleSelection:[],
            showDel : false
        }
    },
    mounted() {
      
    },
    created () {
        this.getListData()
    },
    methods : {

        getListData() {
            this.axios.get('https://jason588.github.io/data.json').then((res) => {
                this.List = res.data.List
            })
        },
        handleSelectionChange(val) {
            this.multipleSelection = val;
            if (this.multipleSelection) {
                this.showDel = true
            }
        }
    },
  
};
</script>

<style lang="scss" scoped>
@import "../assets/scss/base";
.grey-block{
    padding: 30px;
    background-color: #F6F6F6;
    color: $text-dark;
    line-height: 2;
    
}
.table-footer{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 20px;
    .right-text{
        flex: 1;
        text-align: right;
        color: $text-grey;
        line-height: 40px;
    }
}
</style>


